const claimDetailsResponse = {
  status: 'OK',
  response: claimDetails,
};


class ClaimDetailsApi {
  static getClaimDetails(claimId) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const cloneDetails = Object.assign({}, claimDetailsResponse);
        cloneDetails.response.claimId = claimId;
        cloneDetails.response.publicClaimId = claimId;

        resolve(cloneDetails);
      }, delay);
    });
  }

  static getLightClaimDetails(claimId) {
    const details = [{
      claim_id: '10000244',
      claim_detail: 'Hello details 10000244',
    },
    {
      claim_id: '10000232',
      claim_detail: 'Hello details 10000232',
    },
    {
      claim_id: '10000243',
      claim_detail: 'Hello details 10000243',
    },
    {
      claim_id: '10000238',
      claim_detail: 'Hello details 10000238',
    }];

    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const detail = Object.assign({}, details.find(c => c.claim_id === claimId));
        resolve(detail);
      }, delay);
    });
  }
}

export default ClaimDetailsApi;



