import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as navigationActions from '../../redux/actions/navigationActions';

import Header from '../Common/Header';
import NextMeeting from '../NextMeeting';
import NextStep from '../NextStep';
import TimelineList from '../TimelineList';
import EssentialDocList from '../EssentialDocList';
import ContactList from '../ContactList';
import PaymentBloc from '../PaymentBloc';
import HelpBloc from '../HelpBloc';

import './ClaimDetailsPage.css';

class ClaimDetailsPage extends React.Component {
  static downloadDocHandler(event) {
    event.preventDefault();
    alert(event.target.value);
  }

  constructor(props, context) {
    super(props, context);

    this.goToClaimsList = this.goToClaimsList.bind(this);
  }

  goToClaimsList() {
    this.props.actions.navigateToChild('Claims');
  }

  render() {
    const {
      claimId,
      claimStatus,
      lossNotification,
    } = this.props.claimDetails;

    const {
      claimType,
      domain,
      environment,
      appointmentDetails,
      nextStep,
      timelines,
      essentials,
      contacts,
      paymentDetails,
      teaching,
    } = lossNotification;

    return (
      <div>
        <Header
          title="Suivi de litige"
          claim={{ claimId, domain }}
          hasCloseButton={this.props.inPopin} />

        <NextMeeting {...appointmentDetails} />

        <NextStep {...nextStep} />

        <TimelineList timelines={timelines} />

        <EssentialDocList
          essentialDocs={essentials}
          downloadDocAction={ClaimDetailsPage.downloadDocHandler} />

        <ContactList contacts={contacts} />

        <PaymentBloc paymentDetails={paymentDetails} />

        <HelpBloc {...teaching} />

        <button onClick={this.goToClaimsList}>Retourner à la liste</button>
      </div>
    );
  }
}


ClaimDetailsPage.propTypes = {
  claimDetails: PropTypes.object.isRequired,
  inPopin: PropTypes.bool.isRequired,
};


function mapStateToProps(state, ownProps) {
  // state.claims, name of the rootReducer Property

  return {
    claimDetails: state.currentClaimDetails,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(navigationActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ClaimDetailsPage);




