import React from 'react';
import PropTypes from 'prop-types';
import LegalLogo from '../../../assets/legal.png';
import './Header.css';

const Header = ({
  title,
  claim,
  hasCloseButton,
  closeAction,
}) => (
  <div>
    <img src={LegalLogo} />
    <h1>
      {title}{claim && `-n°${claim.claimId}`}
    </h1>
    {claim && <h2>{claim.domain}</h2>}
    {hasCloseButton && (
      <button type="button" onClick={closeAction} value="close">
        X
      </button>
    )}
  </div>
);

Header.propTypes = {
  title: PropTypes.string.isRequired,
  claim: PropTypes.object,
  hasCloseButton: PropTypes.bool,
  closeAction: PropTypes.func,
};

Header.defaultProps = {
  hasCloseButton: false,
  claim: null,
  closeAction: () => {},
};

export default Header;



