import React from 'react';
import PropTypes from 'prop-types';
import TimelineBloc from '../TimelineBloc';
import './TimelineList.css';

/* eslint no-param-reassign: ["error", { "props": false }] */
class TimelineList extends React.Component {
  static processTimelines(timelines) {
    const output = timelines.reduce((groups, timeline) => {
      groups[timeline.day] = groups[timeline.day]
                            ||
                            { key: timeline.day, label: timeline.dayLabel, values: [] };
      // soit group existe déjà / soit new array
      groups[timeline.day].values.push(timeline);
      return groups;
    }, []);

    output.sort((a, b) => b.key - a.key);
    return Object.assign([], output);
  }

  constructor(props, context) {
    super(props, context);

    this.state = {
      groupTimelines: TimelineList.processTimelines(this.props.timelines),
      hideOldEvents: false,
    };

    this.downloadDocHandler = this.downloadDocHandler.bind(this);
    this.showHideOldEvents = this.showHideOldEvents.bind(this);
    this.mapGroupsToBlocs = this.mapGroupsToBlocs.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.timelines.length !== nextProps.timelines.length) {
      this.setState({ groupTimelines: TimelineList.processTimelines(nextProps.timelines) });
    }
  }

  downloadDocHandler(event) {
    event.preventDefault();
    alert(event.target.value);
    return this.props.timelines.length;
  }

  showHideOldEvents(event) {
    event.preventDefault();
    this.setState({ hideOldEvents: !this.state.hideOldEvents });
  }

  mapGroupsToBlocs(groups) {
    const timeLineBloc = (key, dayLabel, timelinesGroup) => (
      <TimelineBloc
        key={key}
        dayLabel={dayLabel}
        timelinesGroup={timelinesGroup}
        downloadDocHandler={this.downloadDocHandler} />
    );

    return groups.map(group =>
      timeLineBloc(group.key, group.label, group.values));
  }

  render() {
    if (this.props.timelines.length === 0) {
      return ('');
    }

    return (
      <div>
        <div>
          <h5>Derniers évènements</h5>
        </div>
        <div>
          <div>
            {this.mapGroupsToBlocs(this.state.groupTimelines.slice(0, 2))}
          </div>
          {(this.state.groupTimelines.length > 2 && this.state.hideOldEvents) && (
          <div>
            {this.mapGroupsToBlocs(this.state.groupTimelines.slice(2))}
          </div>
          )}
          {this.state.groupTimelines.length > 2 &&
            <div>
              <button type="button" onClick={this.showHideOldEvents}>{this.state.hideOldEvents ? 'Masquer les évènements archivés' : 'Afficher les évènements précédents'}</button>
            </div>
          }
        </div>
      </div>
    );
  }
}

TimelineList.propTypes = {
  timelines: PropTypes.array,
};

TimelineList.defaultProps = {
  timelines: [],
};

export default TimelineList;



