import * as types from './actionTypes';

// import claimsApi from '../../api/claimsApi';
import claimsApi from '../../api/mockClaimsApi';
import claimDetailsApi from '../../api/mockClaimDetailsApi';

import { navigateToChild } from './navigationActions';
import { beginAjaxCall, ajaxCallError } from './ajaxSatusActions';
import { loadToken } from './tokenActions';


// When the list of Claims is loaded,
// raising Action Type LOAD_CLAIMS_SUCCESS with the list of Claims
export function loadClaimsSuccess(claims) {
  return { type: types.LOAD_CLAIMS_SUCCESS, claims };
}

export function loadClaimDetailsSuccess(claimDetails) {
  return { type: types.LOAD_CLAIMDETAIL_SUCCESS, claimDetails };
}

// Thunk function :loading the list of Claims, using the claimsApi instance
export function loadClaims() {
  // Calls the getAllClaims methods of the claimsApi implementation
  return (dispatch, getstate) => {
    dispatch(beginAjaxCall());
    const token = getstate().token.value;
    // return dispatch(loadToken()).then(x=>
    dispatch(loadToken());
    claimsApi.getAllClaims(4739542904, token)
      .then((claimsSummary) => {
        dispatch(loadClaimsSuccess(claimsSummary.response));
      })
      .catch((error) => {
        dispatch(ajaxCallError());
        throw (error);
      });
    // );
  };
}

// Thunk function :
// load details of a given claim  => dispatch it to the reducer to add it to the store
export function loadClaimDetails(claimId) {
  return (dispatch, getstate) => {
    dispatch(beginAjaxCall());

    // look up for the claimDetails in the store
    const { claimsDetails } = getstate();
    const claimDetails = claimsDetails.find(c => c.claimId === claimId);

    // if doesn't exist => load it from the API
    if (claimDetails === undefined) {
      return claimDetailsApi.getClaimDetails(claimId)
        .then((cd) => {
          dispatch(loadClaimDetailsSuccess(cd.response));
          dispatch(navigateToChild('ClaimDetails'));
        })
        .catch((error) => {
          dispatch(ajaxCallError());
          throw (error);
        });
    }

    // if exists dispatch it directly
    dispatch(loadClaimDetailsSuccess(claimDetails));
    return dispatch(navigateToChild('ClaimDetails'));
  };
}



