import React from 'react';
import PropTypes from 'prop-types';


const NextStep = ({ title, message }) => {
  if (message === '') {
    return ('');
  }

  return (
    <div>
      <div>
        <h5>{title}</h5>
        <div>
          <div>{message}</div>
        </div>
      </div>
    </div>
  );
};

NextStep.propTypes = {
  title: PropTypes.string,
  message: PropTypes.string,
};

NextStep.defaultProps = {
  title: '',
  message: '',
};

export default NextStep;



