import React from 'react';
import PropTypes from 'prop-types';

import TimelineItem from '../TimelineItem';
import './TimelineBloc.css';

const TimelineBloc = ({
  dayLabel, timelinesGroup, downloadDocHandler,
}) =>
  (
    <div>
      <h5>{dayLabel}</h5>
      <div>
        {timelinesGroup.map(({
 id, message, timelineType, timelineStatus, docId, partyRole, partyName,
}) =>
  (<TimelineItem
    key={id}
    message={message}
    type={timelineType}
    status={timelineStatus}
    paymentBlocAnchor="#paymentBloc"
    downloadAction={downloadDocHandler}
    docId={docId}
    partyRole={partyRole}
    partyName={partyName}
    />))
      }
      </div>
    </div>
  );

TimelineBloc.propTypes = {
  dayLabel: PropTypes.string.isRequired,
  timelinesGroup: PropTypes.array.isRequired,
  downloadDocHandler: PropTypes.func.isRequired,
};

TimelineBloc.defaultProps = {
};

export default TimelineBloc;



