import * as types from '../actions/actionTypes';
import initialState from './initialState';


// tokenReducer
export default function tokenReducer(state = initialState.token, action) {
  switch (action.type) {
    case types.LOAD_TOKEN_SUCCESS:
      return action.token;
    default:
      return state;
  }
}



