import { combineReducers } from 'redux';
import claims from './claimsReducer';
import token from './tokenReducer';
import { claimsDetails, currentClaimDetails } from './claimsDetailsReducer';
import currentChild from './navigationReducer';
import ajaxCallsInProgress from './ajaxStatusReducer';

// Build the rootReducer of the application
const rootReducer = combineReducers({
  claims,
  claimsDetails,
  currentClaimDetails,
  currentChild,
  token,
  ajaxCallsInProgress,
});


export default rootReducer;



