const claimsSummary = {
  status: 'OK',
  response: claims,
};

// Class with the methods to get claims and their details
class ClaimsApi {
  static getAllClaims(policyId, token) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve(Object.assign({}, claimsSummary));
      }, delay);
    });
  }
}

export default ClaimsApi;



