import React from 'react';
import PropTypes from 'prop-types';

import './Popin.css';

/**
 *
 * this component renders a top level modal div that
 * could optionally host the content of JurWidgetRoot
 */

const Popin = ({ content }) => (
  <div id="Popin-modal">
    {content}
  </div>
);

Popin.propTypes = {
  content: PropTypes.node.isRequired,
};

export default Popin;



