import * as types from './actionTypes';
import tokenApi from '../../api/tokenApi';


export function loadTokenSuccess(token) {
  return { type: types.LOAD_TOKEN_SUCCESS, token };
}

export function loadToken() {
  return function (dispatch, getstate) {
    const { token } = getstate();
    console.log(`Etat initial du token ${getstate().token.success}`);
    console.log(`Valeur initial du token ${getstate().token.value}`);
    if (!token.success) {
      return tokenApi.getToken().then((retToken) => {
        dispatch(loadTokenSuccess(retToken));
        console.log(`Etat du token à la fin${getstate().token.success}`);
        console.log(`Valeur du token à la fin${getstate().token.value}`);
      }).catch((error) => {
        throw (error);
      });
    }

    return dispatch(loadTokenSuccess(token));
  };
}



