import React from 'react';
import PropTypes from 'prop-types';
import HelpItem from '../HelpItem';

import './HelpBloc.css';

const HelpBloc = ({ title, teachingDetails }) => {
  let i = 0;
  return (
    <div>
      <div>
        <h5>{title}</h5>
      </div>
      <div>
        {teachingDetails.map(({ question, answers }) => {
          i += 1;
          return <HelpItem key={i} question={question} answers={answers} />;
        })}
      </div>
    </div>
  );
};

HelpBloc.propTypes = {
  title: PropTypes.string.isRequired,
  teachingDetails: PropTypes.array.isRequired,
};

export default HelpBloc;



