import React from 'react';
import PropTypes from 'prop-types';
import LegalLogo from '../../assets/legal.png';
import './ClaimListItem.css';

// Item from a list of Claims
const ClaimListItem = ({ claim, alternate, openDetailsAction }) => (
  <div className={`claim row ${alternate ? 'aletrnate' : ''}`}>
    <div className="col-2">
      <img className="img-fluid img-circle" src={LegalLogo} />
    </div>
    <div className="col-7 claimData">
      <div className="row">
        <h5>
          {claim.caseName} - {claim.lawyerName}
        </h5>
      </div>
      <div className="row">Votre prestation est {claim.claimStatus.label}</div>
      <div className="row claimNumber">
        <label>Prestation N° {claim.claimId}</label>
        {claim.lastUpdateDateLabel && (
        <label>, mis à jour le {claim.lastUpdateDateLabel}</label>
        )}
      </div>
    </div>
    <div className="col-sm-3">
      <button
        type="button"
        className="btn btn-outline-primary btn-lg"
        onClick={openDetailsAction}
        value={claim.claimId}>
        Accéder&nbsp; <i className="fa fa-arrow-right" aria-hidden="true" />
      </button>
    </div>
  </div>
);

// A ClaimListItem is a required object
ClaimListItem.propTypes = {
  claim: PropTypes.object.isRequired,
  alternate: PropTypes.bool,
  openDetailsAction: PropTypes.func.isRequired,
};

ClaimListItem.defaultProps = {
  alternate: false,
};

export default ClaimListItem;



