
class TokenApi {
  // Get the Token from the API
  // Returns a success indicator and the value of the token
  static getToken() {
    return new Promise((resolve, reject) => {
      fetch('https://facebook.github.io/react-native/movies.json')
        .then(response => response.json())
        .then((responseJson) => {
          console.log(`La description est ${responseJson.description}`);
          const t = { success: true, value: responseJson.description };
          resolve(Object.assign({}, t));
        })
        .catch((error) => {
          console.error(error);
        });
    });
  }
}

export default TokenApi;



