import React from 'react';
import PropTypes from 'prop-types';
import './PaymentItem.css';

const PaymentItem = ({
  paymentType, paymentTitle, paymentDate, paymentAmount,
}) => {
  const rowClassName = 'paymentrow';

  /**
  summary rowClassWrapper: fonction qui retourne le nom de class
    à utiliser pour la div conteneur d'une ligne de paiement
  input : paymentType
  output : ex: paymentrow-total, paymentrow-subtotal, paymentrow-detail.
  TODO: valeur de retour à redéfinir
  */
  const rowClassWrapper = () => `${rowClassName}-${paymentType}`;

  /**
  titleClassWrapper: fonction qui retourne le nom de class
    à utiliser pour l'élément "titre du règlement"
  input : paymentType
  output : ex: title-large, title-small TODO: valeur de retour à redéfinir
  */
  const titleClassWrapper = () => {
    if (paymentType === 'total' || paymentType === 'subtotal') {
      return 'title-large';
    }
    return 'title-small';
  };

  return (
    <div className={rowClassWrapper()}>
      <div className={titleClassWrapper()}>{paymentTitle}</div>
      { /* l'élément paymentDate ne s'affiche que
      lorsque paymentDate existe et paymentType == 'detail' */
      (paymentDate && paymentType === 'detail') &&
      <div className="text-right">{paymentDate}</div>
      }
      <div className={`${(paymentType === 'detail') ? 'montant-style-gris' : 'montant-style-bleu-gras'} text-right`}>{paymentAmount}</div>
    </div>);
};

PaymentItem.propTypes = {
  paymentType: PropTypes.string.isRequired,
  paymentTitle: PropTypes.string.isRequired,
  paymentDate: PropTypes.string,
  paymentAmount: PropTypes.string.isRequired,
};

PaymentItem.defaultProps = {
  paymentDate: '',
};

export default PaymentItem;



