import React from 'react';
import PropTypes from 'prop-types';
import { Provider } from 'react-redux';

import JurWidgetRoot from '../JurWidgetRoot';

import configureStore from '../../redux/store';
import initialState from '../../redux/reducers/initialState';
import { loadClaims } from '../../redux/actions/claimsActions';

import './JurWidgetApp.css';

// store definition adding the list of Claims
const store = configureStore();
store.dispatch(loadClaims());

// /TODO: en fonction de certaines props => on peut dipatcher des information dans le store

// JurWidgetApp Component: Provides a customizable widget that displays
//  legal-claims list and their details
// this component handles :
// - redux store and redux provider
// - self contained styles : css or sass

export default function JurWidgetApp(props) {
  return (
    <Provider store={store}>
      <JurWidgetRoot inPopin />
    </Provider>
  );
}



