import React from 'react';
import PropTypes from 'prop-types';

import './EssentialDocItem.css';

const EssentialDocItem = ({
  docLabel, docInfo, docDate, docId, downloadDocAction,
}) => (
  <div>
    <div>
      {docLabel}
    </div>
    {docDate &&
      <span> - {docDate} </span>
    }
    {docInfo &&
      <span> - {docInfo} </span>
    }
    <div>
      <button value={docId} onClick={downloadDocAction}>télécharger</button>
    </div>
  </div>
);

EssentialDocItem.propTypes = {
  docLabel: PropTypes.string.isRequired,
  docInfo: PropTypes.string,
  docDate: PropTypes.string.isRequired,
  docId: PropTypes.string.isRequired,
  downloadDocAction: PropTypes.func.isRequired,
};

EssentialDocItem.defaultProps = {
  docInfo: '',
};

export default EssentialDocItem;



