import React from 'react';
import PropTypes from 'prop-types';
import { isNullOrUndefined } from 'util';

const NextMeeting = ({
  code, title, message, messageDate,
}) => {
  if (isNullOrUndefined(code) || code === '') {
    return (
      ''
    );
  }

  return (
    <div>
      <div>
        <h5>{title}</h5>
        {code === 'EVT_BL_RDV01' ?
          <div>
            {message} <b>{messageDate}</b>
          </div>
            :
          <div>
            <b>{messageDate}</b> {message}
          </div>
            }
      </div>
    </div>
  );
};

NextMeeting.propTypes = {
  code: PropTypes.string.isRequired,
  title: PropTypes.string,
  message: PropTypes.string,
  messageDate: PropTypes.string,
};

NextMeeting.defaultProps = {
  title: '',
  message: '',
  messageDate: '',
};

export default NextMeeting;



