import React from 'react';
import PropTypes from 'prop-types';

import ClaimListItem from '../ClaimListItem';
import './ClaimList.css';

// List of Claims
const ClaimList = ({ openedClaims, closedClaims, goToDetails }) => {
  let odd = 0;
  const alternateWrapper = () => {
    odd += 1;
    if ((odd % 2) !== 0) {
      return true;
    }
    return false;
  };

  return (
    <div className="claimsList">
      <div className="item">
        <h5>Prestations en cours</h5>
        {openedClaims.map(claim =>
          (<ClaimListItem
            key={claim.claimId}
            claim={claim}
            alternate={alternateWrapper()}
            openDetailsAction={goToDetails}
              />))
        }
      </div>

      <div className="item">
        <h5>Prestations closes</h5>
        {closedClaims.map(claim =>
          (<ClaimListItem
            key={claim.claimId}
            claim={claim}
            alternate={alternateWrapper()}
            openDetailsAction={goToDetails}
            />))
        }
      </div>
    </div>
  );
};

// ClaimList requires a list of Claims, which can be empty
ClaimList.propTypes = {
  openedClaims: PropTypes.array,
  closedClaims: PropTypes.array,
  goToDetails: PropTypes.func.isRequired,
};

ClaimList.defaultProps = {
  openedClaims: [],
  closedClaims: [],
};

export default ClaimList;



