import * as types from '../actions/actionTypes';
import initialState from './initialState';

export function claimsDetails(state = initialState.claimsDetails, action) {
  switch (action.type) {
    // When successfully receive a claimDetails, add it or replace it in the claimsDetails state
    case types.LOAD_CLAIMDETAIL_SUCCESS:
      return [
        // insert or replace the loaded claimDetails
        ...state.filter(cd => cd.claimId !== action.claimDetails.claimId),
        Object.assign({}, action.claimDetails),
      ];
    default:
      return state;
  }
}

export function currentClaimDetails(state = initialState.currentClaimDetails, action) {
  switch (action.type) {
    // When successfully receive a claimDetails=>update the currentClaimDetails state
    case types.LOAD_CLAIMDETAIL_SUCCESS:
      return Object.assign({}, action.claimDetails);
    default:
      return state;
  }
}




