import React from 'react';
import PropTypes from 'prop-types';
import { isNullOrUndefined } from 'util';

import PaymentItem from '../PaymentItem';
import './PaymentBloc.css';

const PaymentBloc = ({ paymentDetails }) => {
  const {
    title,
    totalAmount,
    totalAmountLabel,
    customerPaymentDetails,
    tiersPaymentDetails,
  } = paymentDetails;

  const hasCustomerDetails =
    customerPaymentDetails &&
    customerPaymentDetails.paymentDetails &&
    customerPaymentDetails.paymentDetails.length > 0;

  const hasTiersDetails =
    tiersPaymentDetails &&
    tiersPaymentDetails.paymentDetails &&
    tiersPaymentDetails.paymentDetails.length > 0;

  if (isNullOrUndefined(paymentDetails) || totalAmount === 0) {
    return '';
  }

  let i = 0;
  return (
    <div className="row payments">
      <div className="">
        <h5>Nos règlements</h5>
      </div>
      <div className="">
        <PaymentItem
          paymentType="total"
          paymentTitle={title}
          paymentAmount={`${totalAmountLabel} €`}
        />
        {hasCustomerDetails && (
          <PaymentItem
            paymentType="subtotal"
            paymentTitle={customerPaymentDetails.title}
            paymentAmount={`${customerPaymentDetails.totalAmountLabel} €`}
          />
        )}
        {hasCustomerDetails &&
          customerPaymentDetails.paymentDetails.map((payment) => {
            i += 1;
            return (<PaymentItem
              key={i.toString()}
              paymentType="detail"
              paymentTitle="Règlement"
              paymentDate={payment.dateLabel}
              paymentAmount={`${payment.amountLabel} €`} />);
        })}
        {hasTiersDetails && (
          <PaymentItem
            paymentType="subtotal"
            paymentTitle={tiersPaymentDetails.title}
            paymentAmount={`${tiersPaymentDetails.totalAmountLabel} €`}
          />
        )}
        {hasTiersDetails &&
          tiersPaymentDetails.paymentDetails.map((payment) => {
            i += 1;
            return (<PaymentItem
              key={i.toString()}
              paymentType="detail"
              paymentTitle="Règlement"
              paymentDate={payment.dateLabel}
              paymentAmount={`${payment.amountLabel} €`} />);
        })}
      </div>
    </div>
  );
};

PaymentBloc.propTypes = {
  paymentDetails: PropTypes.object.isRequired,
};

export default PaymentBloc;



