import React from 'react';
import PropTypes from 'prop-types';

import EssentialDocItem from '../EssentialDocItem';

const EssentialDocList = ({ essentialDocs, downloadDocAction }) => {
  if (essentialDocs.length === 0) {
    return ('');
  }
  return (
    <div>
      <div><h5>Vos documents essentiels</h5></div>
      <div>
        {essentialDocs.map(({
 docId, message, role, dateLabel,
}) =>
          (<EssentialDocItem
            key={docId}
            docId={docId}
            docLabel={message}
            docInfo={role}
            docDate={dateLabel}
            downloadDocAction={downloadDocAction}
          />))
        }
      </div>
    </div>
  );
};

EssentialDocList.propTypes = {
  essentialDocs: PropTypes.array,
  downloadDocAction: PropTypes.func.isRequired,
};

EssentialDocList.defaultProps = {
  essentialDocs: [],
};

export default EssentialDocList;



