import * as types from '../actions/actionTypes';
import initialState from './initialState';

// claimsReducer
export default function claimsReducer(state = initialState.claims, action) {
  switch (action.type) {
    // When successfully receive the list of Claims, return the claims
    case types.LOAD_CLAIMS_SUCCESS:
      return action.claims;
    default:
      return state;
  }
}



