import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import ClaimsPage from '../ClaimsPage'; // List of Claims Component
import ClaimDetailsPage from '../ClaimDetailsPage'; // Claim details page
import Loading from '../Common/Loading';
import Popin from '../Common/Popin';
import './JurWidgetRoot.css';

// /TODO : delete bootstrap from the widget
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../../../node_modules/font-awesome/css/font-awesome.min.css';

// JurWidgetRoot Component, managing the display of a list of Claims and their Details
// This class will be included in a Provider to be able to link with the reducers,
// stores and actions
// This class handle routing via logic method based on props and stats
const JurWidgetRoot = ({ childName, loading, inPopin }) => {
  let Child;
  switch (childName) {
    case 'Claims':
      Child = ClaimsPage;
      break;
    case 'ClaimDetails':
      Child = ClaimDetailsPage;
      break;
    default:
      Child = ClaimsPage;
  }

  const content = (
    <div id="jur-widget-root" className="container-fluid">
      <div id="claimPjItem">
        <Loading loading={loading} />
        <Child inPopin={inPopin} />
      </div>
    </div>
  );

  return (inPopin && <Popin content={content} />) || content;
};

JurWidgetRoot.propTypes = {
  childName: PropTypes.string.isRequired,
  loading: PropTypes.bool.isRequired,
  inPopin: PropTypes.bool.isRequired,
};

function mapStateToProps(state, ownProps) {
  return {
    childName: state.currentChild,
    loading: state.ajaxCallsInProgress > 0,
  };
}

export default connect(mapStateToProps)(JurWidgetRoot);



