import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as claimsActions from '../../redux/actions/claimsActions';
import ClaimList from '../ClaimList';
import Header from '../Common/Header';

import './ClaimsPage.css';

class ClaimsPage extends React.Component {
  static filterClaims(status, claims) {
    const filteredClaims = claims.filter(c => c.claimStatus.code === status);
    filteredClaims.sort((a, b) => b.last_step_update_date - a.last_step_update_date);
    return filteredClaims;
  }

  constructor(props, context) {
    super(props, context);

    this.state = {
      openedClaims: ClaimsPage.filterClaims('OPEN', this.props.claims),
      closedClaims: ClaimsPage.filterClaims('CLOSED', this.props.claims),
      unassignedClaims: ClaimsPage.filterClaims('AAffecter', this.props.claims),
    };

    this.goToClaimDetails = this.goToClaimDetails.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.claims.length !== nextProps.claims.length) {
      this.setState({ openedClaims: ClaimsPage.filterClaims('OPEN', nextProps.claims) });
      this.setState({ closedClaims: ClaimsPage.filterClaims('OPEN', nextProps.claims) });
      this.setState({ unassignedClaims: ClaimsPage.filterClaims('AAffecter', nextProps.claims) });
    }
  }

  goToClaimDetails(event) {
    event.preventDefault();

    const claimId = event.target.value;
    if (claimId == null) {
      return;
    }

    this.props.actions.loadClaimDetails(claimId);
  }

  render() {
    return (
      <div>
        <Header
          title="Suivi de litige"
          hasCloseButton={this.props.inPopin}
        />
        <ClaimList
          openedClaims={[...this.state.openedClaims, ...this.state.unassignedClaims]}
          closedClaims={this.state.closedClaims}
          goToDetails={this.goToClaimDetails}
        />
      </div>
    );
  }
}

// ClaimsPage needs a list of claims
ClaimsPage.propTypes = {
  claims: PropTypes.array.isRequired,
  inPopin: PropTypes.bool.isRequired,
};


function mapStateToProps(state, ownProps) {
  // state.claims, name of the rootReducer Property
  return {
    claims: state.claims,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(claimsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ClaimsPage);




